import {DomainResource} from '../domain-resource';
import {Identifier} from '../terminology/identifier';
import {Reference} from '../reference';
import {CodeableConcept} from '../terminology/codeableconcept';
import {HumanName} from './humanname';
import {ContactPoint} from '../terminology/contactpoint';
import {Address} from './address';
import {Attachment} from './attachment';
import {Period} from '../period';

export class RelatedPerson extends DomainResource {
    resourceType: string = 'RelatedPerson';
    identifier?: Identifier[];
    active?: boolean;
    patient: Reference; // (Patient)
    relationship?: CodeableConcept[];
    name?: HumanName[];
    telecom?: ContactPoint[];
    gender?: 'male' | 'female' | 'other' | 'unknown';
    birthDate?: Date; // Regex: ([0-9]([0-9]([0-9][1-9]|[1-9]0)|[1-9]00)|[1-9]000)(-(0[1-9]|1[0-2])(-(0[1-9]|[1-2][0-9]|3[0-1]))?)?
    address?: Address[];
    photo?: Attachment[];
    period?: Period;
    communication?: { // BackboneElement
        language: CodeableConcept;
        preferred: boolean;
    }[];
}
