import {DomainResource} from '../domain-resource';
import {Attachment, CodeableConcept, Coding, Identifier, Money, Period, Quantity, Reference, SimpleQuantity} from '..';
import {Timing} from '../timing';
import {Annotation} from '../workflow';
import {Signature} from '../signature';

export class Contract extends DomainResource {

    resourceType: string = 'Contract';
    identifier?: Identifier[];
    url?: string; // uri
    version?: string;
    status?: 'amended' | 'appended' | 'cancelled' | 'disputed' | 'entered-in-error' |
        'executable' | 'executed' | 'negotiable' | 'offered' | 'policy' | 'rejected' | 'renewed' | 'revoked' | 'resolved' | 'terminated';
    legalState?: CodeableConcept;
    instantiatesCanonical?: Reference; // (Contract)
    instantiatesUri?: string; // uri
    contentDerivative?: CodeableConcept;
    issued?: Date;
    applies?: Period;
    expirationType?: CodeableConcept;
    subject?: Reference[]; // (Any)
    authority?: Reference[]; // (Organization)
    domain?: Location[]; // (Location)
    site?: Location[]; // (Location)
    name?: string;
    title?: string;
    subtitle?: string;
    alias?: string[];
    author?: Reference; // (Patient | Practitioner | PractitionerRole | Organization)
    scope?: CodeableConcept;
    topicCodeableConcept?: CodeableConcept;
    topicReference?: Reference; // (Any)
    type?: CodeableConcept;
    subType?: CodeableConcept[];
    contentDefinition?: { // BackboneElement
        type: CodeableConcept;
        subType?: CodeableConcept;
        publisher?: Reference;
        publicationDate?: Date;
        publicationStatus: 'amended' | 'appended' | 'cancelled' | 'disputed' | 'entered-in-error' |
            'executable' | 'executed' | 'negotiable' | 'offered' | 'policy' | 'rejected' | 'renewed' | 'revoked' | 'resolved' | 'terminated';
        copyright?: string; // markdown
    };
    term?: { // BackboneElement
        identifier?: Identifier;
        issued?: Date;
        applies?: Period;
        topicCodeableConcept?: CodeableConcept;
        topicReference?: Reference; // (Any)
        type?: CodeableConcept;
        subType?: CodeableConcept;
        text?: string;
        securityLabel?: { // BackboneElement
            number?: number[];
            classification: Coding;
            category?: Coding;
            control?: Coding[];
        }[];
        offer: { // BackboneElement
            identifier?: Identifier[];
            party?: { // BackboneElement
                reference: Reference[]; // (Patient | RelatedPerson | Practitioner | PractitionerRole | Device | Group | Organization)
                role: CodeableConcept;
            }[];
            topic?: Reference; // (Any)
            type?: CodeableConcept;
            decision?: CodeableConcept;
            decisionMode?: CodeableConcept[];
            answer?: {
                valueBoolean: boolean;
                valueDecimal: number; // decimal
                valueInteger: number; // integer
                valueDate: Date;
                valueDateTime: Date;
                valueTime: Date;
                valueString: string;
                valueUri: string; // uri
                valueAttachment: Attachment;
                valueCoding: Coding;
                valueQuantity: Quantity;
                valueReference: Reference; // (Any)
            }[];
            text?: string;
            linkId?: string[];
            securityLabelNumber?: number[];
        };
        asset?: {
            scope?: CodeableConcept;
            type?: CodeableConcept[];
            typeReference?: Reference[]; // (Any)
            subtype?: CodeableConcept[];
            relationship?: Coding;
            context?: { // BackboneElement
                reference?: Reference; // (Any)
                code?: CodeableConcept[];
                text?: string;
            }[];
            condition?: string;
            periodType?: CodeableConcept[];
            period?: Period[];
            usePeriod?: Period[];
            text?: string;
            linkId?: string[];
            answer?: Reference[]; // Reference to another Element
            securityLabelNumber?: number[];
            valuedItem?: { // BackboneElement
                entityCodeableConcept?: CodeableConcept;
                entityReference?: Reference; // (Any)
                identifier?: Identifier;
                effectiveTime?: Date;
                quantity?: SimpleQuantity;
                unitPrice?: Money;
                factor?: number; // decimal
                points?: number; // decimal
                net?: Money;
                payment?: string;
                paymentDate?: Date;
                responsible?: Reference; // (Organization | Patient | Practitioner | PractitionerRole | RelatedPerson)
                recipient?: Reference; // (Organization | Patient | Practitioner | PractitionerRole | RelatedPerson)
                linkId?: string[];
                securityLabelNumber?: number[];
            }[];
        }[];
        action?: { // BackboneElement
            doNotPerform?: boolean;
            type: CodeableConcept;
            subject?: {
                reference: Reference[]; // (Patient | RelatedPerson | Practitioner | PractitionerRole | Device | Group | Organization)
                role?: CodeableConcept;
            }[];
            intent: CodeableConcept;
            linkId?: string[];
            status: CodeableConcept;
            context?: Reference; // (Encounter | EpisodeOfCare)
            contextLinkId?: string[];
            occurrenceDateTime?: Date;
            occurrencePeriod?: Period;
            occurrenceTiming?: Timing;
            requester?: Reference[]; // (Patient | RelatedPerson | Practitioner | PractitionerRole | Device | Group | Organization)
            requesterLinkId?: string[];
            performerType?: CodeableConcept[];
            performerRole?: CodeableConcept;
            performer?: Reference; // (RelatedPerson | Patient | Practitioner | PractitionerRole | CareTeam | Device | Substance | Organization | Location)
            performerLinkId?: string[];
            reasonCode?: CodeableConcept[];
            reasonReference?: Reference[]; // (Condition | Observation | DiagnosticReport | DocumentReference | Questionnaire | QuestionnaireResponse)
            reason?: string[];
            reasonLinkId?: string[];
            note?: Annotation[];
            securityLabelNumber?: number[];
        }[];
        group?: Reference[]; // Reference to another Element
    }[];
    supportingInfo?: Reference[]; // (Any)
    relevantHistory?: Reference[]; // (Provenance)
    signer?: { // BackboneElement
        type: Coding;
        party: Reference; // (Organization | Patient | Practitioner | PractitionerRole | RelatedPerson)
        signature: Signature[];
    }[];
    friendly?: { // BackboneElement
        contentAttachment: Attachment;
        contentReference: Reference; // (Composition | DocumentReference | QuestionnaireResponse)
    }[];
    legal?: {
        contentAttachment: Attachment;
        contentReference: Reference; // (Composition | DocumentReference | QuestionnaireResponse)
    }[];
    rule?: {
        contentAttachment: Attachment;
        contentReference: Reference; // (DocumentReference)
    }[];
    legallyBindingAttachment?: Attachment;
    legallyBindingReference?: Reference; // (Composition | DocumentReference | QuestionnaireResponse | Contract)
}
