import {SimpleQuantity} from './terminology/simple-quantity';
import {Element} from './element';

export class Range extends Element {
    constructor(source?: any) {
        super();
        if (source)
            for (const key of Object.keys(source)) {
                this[key] = source[key];
            }
    }
    low?: SimpleQuantity; // SimpleQuantity Low limit
    high?: SimpleQuantity; // SimpleQuantity High limit
}
