import { DomainResource } from '../domain-resource';
import { Identifier } from '..';
import { HumanName } from './humanname';
import { ContactPoint } from '..';
import { Address } from './address';
import { CodeableConcept } from '..';
import { Attachment } from './attachment';
import { Reference } from '../reference';
import { Period } from '../period';

export class Patient extends DomainResource {
  constructor(source?: any) {
    super();
    if (source)
      for (const key of Object.keys(source)) {
        this[key] = source[key];
      }
  }
  resourceType: string = 'Patient';
  identifier?: Identifier[];
  active?: boolean;
  name?: HumanName[];
  telecom?: ContactPoint[];
  gender?: 'male' | 'female' | 'other' | 'unknown';
  birthDate?: Date;
  deceasedBoolean?: boolean;
  deceasedDateTime?: Date;
  address?: Address[];
  maritalStatus?: CodeableConcept;
  multipleBirthBoolean?: boolean;
  multipleBirthInteger?: number; // Regex: [0]|[-+]?[1-9][0-9]*
  photo?: Attachment[];
  contact?: { // BackboneElement
    relationship?: CodeableConcept[];
    name?: HumanName;
    telecom?: ContactPoint[];
    address?: Address;
    gender?: 'male' | 'female' | 'other' | 'unknown';
    organization?: Reference; // (Organization)
    period?: Period;
  }[];
  communication?: { // BackboneElement
    language: CodeableConcept;
    preferred?: boolean;
  }[];
  generalPractitioner?: Reference[]; // (Organization | Practitioner | PractitionerRole)
  managingOrganization?: Reference; // (Organization)
  link?: { // BackboneElement
    other: Reference; // (Patient | RelatedPerson)
    type: 'replaced-by' | 'replaces' | 'refer' | 'seealso';
  }[];

  public static getNew() {
    return new Patient({name: [HumanName.getNew()]});
  }
}
