import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'fhirEmailExtract'
})
export class FhirEmailExtractPipe implements PipeTransform {

    transform(value: any, ...args: any[]): string {
        let email: string = '';
        if (value && value.length > 0) {
            for (let contactPoint of value) {
                if (contactPoint.system === 'email') email = email.concat(contactPoint.value, ', ');
            }
        }
        return email.trim().slice(0, email.length - 2);
    }

}
