import {DomainResource} from '../domain-resource';
import {Identifier} from './identifier';
import {ContactDetail} from './contactdetail';
import {UsageContext} from './usagecontext';
import {CodeableConcept} from './codeableconcept';
import {Coding} from './coding';
import {ValueSetContains} from '../valuesetcontains';

export class ValueSet extends DomainResource {
    resourceType: string = 'ValueSet';
    url?: string; // uri
    identifier?: Identifier[];
    version?: string;
    name?: string;
    title?: string;
    status: 'draft' | 'active' | 'retired' | 'unknown';
    experimental?: boolean;
    date?: Date;
    publisher?: string;
    contact?: ContactDetail[];
    description?: string;
    useContext?: UsageContext[];
    jurisdiction?: CodeableConcept[];
    immutable?: boolean;
    purpose?: string; // Regex: \s*(\S|\s)*
    copyright?: string; // Regex: \s*(\S|\s)*
    compose?: { // BackboneElement
        lockedDate?: Date; // Regex: ([0-9]([0-9]([0-9][1-9]|[1-9]0)|[1-9]00)|[1-9]000)(-(0[1-9]|1[0-2])(-(0[1-9]|[1-2][0-9]|3[0-1]))?)?
        inactive?: boolean;
        include: { // BackboneElement
            system?: string; // uri
            version?: string;
            concept?: {
                code: string; // code
                display?: string;
                designation?: { // BackboneElement
                    language?: string; // code
                    use?: Coding;
                    value: string;
                }[]
            }[];
            filter?: { // BackboneElement
                property: string; // code
                op: string; // = | is-a | descendent-of | is-not-a | regex | in | not-in | generalizes | exists
                value: string;
            }[];
            valueSet?: string[]; // canonical(ValueSet)
        }[];
        exclude?: { // BackboneElement
            system?: string; // uri
            version?: string;
            concept?: {
                code: string; // code
                display?: string;
                designation?: { // BackboneElement
                    language?: string; // code
                    use?: Coding;
                    value: string;
                }[]
            }[];
            filter?: { // BackboneElement
                property: string; // code
                op: string; // = | is-a | descendent-of | is-not-a | regex | in | not-in | generalizes | exists
                value: string;
            }[];
            valueSet?: string[]; // canonical(ValueSet)
        }[]; // see include	Explicitly exclude codes from a code system or other value sets
    };
    expansion?: { // BackboneElement
        identifier?: string; // uri
        timestamp: Date;
        total?: number; // Regex: [0]|[-+]?[1-9][0-9]*
        offset?: number; // Regex: [0]|[-+]?[1-9][0-9]*
        parameter?: {
            name: string;
            valueString?: string;
            valueBoolean?: boolean;
            valueInteger?: number;
            valueDecimal?: number;
            valueUri?: string;
            valueCode?: string;
            valueDateTime?: Date;
        }[]; // BackboneElement
        contains?: ValueSetContains[];
    };
}
