import {DomainResource} from '../domain-resource';
import {CodeableConcept, Identifier, Reference} from '..';

export class AppointmentResponse extends DomainResource {
    resourceType: string = 'AppointmentResponse';
    identifier?: Identifier[];
    appointment: Reference; // (Appointment)
    start?: Date; // Regex: ([0-9]([0-9]([0-9][1-9]|[1-9]0)|[1-9]00)|[1-9]000)-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])T([01][0-9]|2[0-3]):[0-5][0-9]:([0-5][0-9]|60)(\.[0-9]+)?(Z|(\+|-)((0[0-9]|1[0-3]):[0-5][0-9]|14:00))
    end?: Date;
    participantType?: CodeableConcept[];
    actor?: Reference; // (Patient | Practitioner | PractitionerRole | RelatedPerson | Device | HealthcareService | Location)
    participantStatus: 'accepted' | 'declined' | 'tentative' | 'in-process' | 'completed' | 'needs-action' | 'entered-in-error';
    comment?: string;
}
