import {DomainResource} from '../domain-resource';
import {CodeableConcept, Coding, ContactPoint, Identifier, Period, Reference} from '..';

export class Endpoint extends DomainResource {
  resourceType: string = 'Endpoint';
  identifier?: Identifier[];
  status: 'active' | 'suspended' | 'error' | 'off' | 'entered-in-error' | 'test';
  connectionType: Coding;
  name?: string;
  managingOrganization?: Reference; // (Organization)
  contact?: ContactPoint[];
  period?: Period;
  payloadType: CodeableConcept[];
  payloadMimeType?: string; // Regex: [^\s]+(\s[^\s]+)*
  address: string; // url
  header?: string[];
}
