import {Identifier} from './identifier';
import {ContactDetail} from './contactdetail';
import {UsageContext} from './usagecontext';
import {CodeableConcept} from './codeableconcept';
import {DomainResource} from '../domain-resource';
import {Coding} from './coding';
import {CodeSystemConcept} from './codesystemconcept';

export class CodeSystem extends DomainResource {
    resourceType: string = 'CodeSystem';
    url?: string; // uri
    identifier?: Identifier[];
    version?: string;
    name?: string;
    title?: string;
    status: 'draft' | 'active' | 'retired' | 'unknown';
    experimental?: boolean;
    date?: Date;
    publisher?: string;
    contact?: ContactDetail[];
    description?: string; // Regex: \s*(\S|\s)*
    useContext?: UsageContext[];
    jurisdiction?: CodeableConcept[];
    purpose?: string; // Regex: \s*(\S|\s)*
    copyright?: string; // Regex: \s*(\S|\s)*
    caseSensitive?: boolean;
    valueSet?: string; // canonical(ValueSet)
    hierarchyMeaning?: 'grouped-by' | 'is-a' | 'part-of' | 'classified-with';
    compositional?: boolean;
    versionNeeded?: boolean;
    content: 'not-present' | 'example' | 'fragment' | 'complete' | 'supplement';
    supplements?: string; // canonical(CodeSystem)
    count?: number; // unsignedInt Regex: [0]|([1-9][0-9]*)
    filter?: { // BackboneElement
        code: string; // code
        description?: string;
        operator: string[]; // code
        value: string;
    }[];
    property?: { // BackboneElement
        code: string; // code
        uri?: string; // uri
        description?: string;
        type: 'Coding' | 'string' | 'boolean' | 'Date' | 'number'; // code,(	code | Coding | string | integer | boolean | dateTime | decimal)
    }[];
    /*concept?: { // BackboneElement
        code: string;
        display?: string;
        definition?: string;
        designation?: { // BackboneElement
            language?: string;
            use?: Coding;
            value: string;
        }[];
        property?: { // BackboneElement
            code: string;
            valueCode: string;
            valueCoding: Coding;
            valueString: string;
            valueInteger: number; // integer
            valueBoolean: boolean;
            valueDateTime: Date;
            valueDecimal: number; // decimal
        }[];*/
       concept?: CodeSystemConcept[];
}
