import {DomainResource} from '../domain-resource';
import {Reference} from '../reference';
import {CodeableConcept, Signature} from '..';
import {Timing} from '../timing';

export class VerificationResult extends DomainResource {
    resourceType: string = 'VerificationResult';
    target?: Reference[]; // (Any)
    targetLocation?: string[];
    need?: CodeableConcept; // none | initial | periodic
    status: 'attested' | 'validated' | 'in-process' | 'req-revalid' | 'val-fail' | 'reval-fail';
    statusDate?: Date;
    validationType?: CodeableConcept; // nothing | primary | multiple
    validationProcess?: CodeableConcept[];
    frequency?: Timing;
    lastPerformed?: Date;
    nextScheduled?: Date; // Regex: ([0-9]([0-9]([0-9][1-9]|[1-9]0)|[1-9]00)|[1-9]000)(-(0[1-9]|1[0-2])(-(0[1-9]|[1-2][0-9]|3[0-1]))?)?
    failureAction?: CodeableConcept;
    primarySource?: { // BackboneElement
        who?: Reference; // (Organization | Practitioner | PractitionerRole)
        type?: CodeableConcept[];
        communicationMethod?: CodeableConcept[];
        validationStatus?: CodeableConcept; // successful | failed | unknown
        validationDate?: Date;
        canPushUpdates?: CodeableConcept; // yes | no | undetermined
        pushTypeAvailable?: CodeableConcept[];
    }[];
    attestation?: { // BackboneElement
        who?: Reference; // (Practitioner | PractitionerRole | Organization)
        onBehalfOf?: Reference; // (Organization | Practitioner | PractitionerRole)
        communicationMethod?: CodeableConcept;
        date?: Date; // Regex: ([0-9]([0-9]([0-9][1-9]|[1-9]0)|[1-9]00)|[1-9]000)(-(0[1-9]|1[0-2])(-(0[1-9]|[1-2][0-9]|3[0-1]))?)?
        sourceIdentityCertificate?: string;
        proxyIdentityCertificate?: string;
        proxySignature?: Signature;
        sourceSignature?: Signature;
    };
    validator?: { // BackboneElement
        organization: Reference; // (Organization)
        identityCertificate?: string;
        attestationSignature?: Signature;
    }[];
}
