import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'fhirTelecomWebExtract'
})
export class FhirTelecomWebExtractPipe implements PipeTransform {

  transform(value: any, ...args: any[]): string {
    let url: string = '';
    if (value && value.length > 0) {
      for (let contactPoint of value) {
        if (contactPoint.system === 'url') url = url.concat(contactPoint.value, ', ');
      }
    }
    return url.trim().slice(0,url.length-2);
  }

}
