import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'fhirAddressLineExtract',
})
export class FhirAddressLineExtract implements PipeTransform {

    transform(value: any, ...args: any[]): string {
        if (value && value[0] && value[0].line && value[0].line[0]) return value[0].line[0];
        return '';
    }

}
