import {DomainResource} from '../domain-resource';
import {CodeableConcept, Identifier, Period, Reference} from '..';
import {Annotation} from '../workflow/annotation';
import {SimpleQuantity} from '../terminology/simple-quantity';
import {Ratio} from '../ratio';

export class MedicationAdministration extends DomainResource {
    resourceType: string = 'MedicationAdministration';
    identifier?: Identifier[];
    instantiates?: string[]; // uri
    partOf?: Reference[]; // (MedicationAdministration | Procedure)
    status: 'in-progress' | 'not-done' | 'on-hold' | 'completed' | 'entered-in-error' | 'stopped' | 'unknown';
    statusReason?: CodeableConcept[];
    category?: CodeableConcept;
    medicationCodeableConcept: CodeableConcept;
    medicationReference: Reference; // (Medication)
    subject: Reference; // (Patient | Group)
    context?: Reference; // (Encounter | EpisodeOfCare)
    supportingInformation?: Reference[]; // (Any)
    effectiveDateTime: Date;
    effectivePeriod: Period;
    performer?: { // BackboneElement
        function?: CodeableConcept;
        actor: Reference; // (Practitioner | PractitionerRole | Patient | RelatedPerson | Device)
    }[];
    reasonCode?: CodeableConcept[];
    reasonReference?: Reference[]; // (Condition | Observation | DiagnosticReport)
    request?: Reference; // (MedicationRequest)
    device?: Reference[]; // (Device)
    note?: Annotation[];
    dosage?: {
        text?: string;
        site?: CodeableConcept;
        route?: CodeableConcept;
        method?: CodeableConcept;
        dose?: SimpleQuantity;
        rateRatio?: Ratio;
        rateQuantity?: SimpleQuantity;
    };
    eventHistory?: Reference[]; // (Provenance)
}
