import {Coding} from './coding';
import {Element} from '../element';

export class CodeableConcept extends Element {
    constructor(source?: any) {
        super();
        if (source)
            for (const key of Object.keys(source)) {
                this[key] = source[key];
            }
    }
    coding?: Coding[];
    text?: string; // Regex: [ \r\n\t\S]+
}
