import {DomainResource} from '../domain-resource';
import {ContactDetail} from './contactdetail';
import {UsageContext} from './usagecontext';
import {CodeableConcept} from './codeableconcept';

export class TerminologyCapabilities extends DomainResource {
    resourceType: string = 'TerminologyCapabilities';
    url?: string; // uri
    version?: string;
    name?: string;
    title?: string;
    status: 'draft' | 'active' | 'retired' | 'unknown';
    experimental?: boolean;
    date: Date;
    publisher?: string;
    contact?: ContactDetail[];
    description?: string;
    useContext?: UsageContext[];
    jurisdiction?: CodeableConcept[];
    purpose?: string; // Regex: \s*(\S|\s)*
    copyright?: string; // Regex: \s*(\S|\s)*
    kind: 'instance' | 'capability' | 'requirements';
    software?: {
        name: string;
        version?: string;
    };
    implementation?: { // BackboneElement
        description: string;
        url?: string; // url
    };
    lockedDate?: boolean;
    codeSystem?: { // BackboneElement
        uri?: string; // 	canonical(CodeSystem)
        version?: { // BackboneElement
            code?: string;
            isDefault?: boolean;
            compositional?: boolean;
            language?: string[];
            filter?: {
                code: string; // code
                op: string[];
            }[];
            property?: string[]; // code
        }[];
        subsumption?: boolean;
    }[];
    expansion?: {
        hierarchical?: boolean;
        paging?: boolean;
        incomplete?: boolean;
        parameter?: {
            name: string;
            documentation?: string;
        }[];
        textFilter?: string;
    };
    codeSearch?: 'explicit' | 'all'; // code
    validateCode?: { // BackboneElement
        translations: boolean;
    };
    translation?: { // BackboneElement
        needsMap: boolean;
    };
    closure?: { // BackboneElement
        translation?: boolean;
    };
}
