import {DomainResource} from '../domain-resource';
import {CodeableConcept, Identifier, Period, Reference} from '..';

export class Appointment extends DomainResource {
    resourceType: string = 'Appointment';
    identifier?: Identifier[];
    status: 'proposed' | 'pending' | 'booked' | 'arrived' | 'fulfilled' | 'cancelled' | 'noshow' | 'entered-in-error' | 'checked-in' | 'waitlist';
    cancelationReason?: CodeableConcept;
    serviceCategory?: CodeableConcept[];
    serviceType?: CodeableConcept[];
    specialty?: CodeableConcept[];
    appointmentType?: CodeableConcept;
    reasonCode?: CodeableConcept[];
    reasonReference?: Reference[]; // (Condition | Procedure | Observation | ImmunizationRecommendation)
    priority?: number; // unsignedInt [0]|([1-9][0-9]*)
    description?: string;
    supportingInformation?: Reference[]; // (Any)
    start?: Date; // instant Regex: ([0-9]([0-9]([0-9][1-9]|[1-9]0)|[1-9]00)|[1-9]000)-(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])T([01][0-9]|2[0-3]):[0-5][0-9]:([0-5][0-9]|60)(\.[0-9]+)?(Z|(\+|-)((0[0-9]|1[0-3]):[0-5][0-9]|14:00))
    end?: Date; // instant
    minutesDuration?: number; // positiveInt
    slot?: Reference[]; // (Slot)
    created?: Date;
    comment?: string;
    patientInstruction?: string;
    basedOn?: Reference[]; // (ServiceRequest)
    participant: { // BackboneElement
        type?: CodeableConcept[];
        actor?: Reference; // (Patient | Practitioner | PractitionerRole | RelatedPerson | Device | HealthcareService | Location)
        required?: 'required' | 'optional' | 'information-only';
        status: 'accepted' | 'declined' | 'tentative' | 'needs-action';
        period?: Period;
    }[];
    requestedPeriod?: Period[];

}
