import {HttpParams} from "@angular/common/http";

export class InstanceLevelInteractions {
    public static read: number = 0;
    public static vread: number = 1;
    public static update: number = 2;
    public static patch: number = 3;
    public static delete: number = 4;
    public static history: number = 5;
    public static create: number = 6;
}

export class TypeLevelInteractions {
    public static create: number = 10;
    public static search: number = 11;
    public static history: number = 12;
}

export class WholeSystemInteractions {
    public static capabilities: number = 21;
    public static batch: number = 22;
    public static search: number = 23;
    public static history: number = 24;
}

export class CommonSearchParameters {
    public static _id: string = '_id';
    public static _lastUpdated: string = '_lastUpdated';
    public static _tag: string = '_tag';
    public static _profile: string = '_profile';
    public static _security: string = '_security';
    public static _text: string = '_text';
    public static _content: string = '_content';
    public static _list: string = '_list';
    public static _query: string = '_query';
}

export class SearchControlParameters {

}

export class FhirQueryBuilder {

    private readonly _baseUrl: string = '';
    private _resourceName: string;
    private _params = [];
    private _fhirOperation: number;

    constructor(baseUrl?: string) {
        this._baseUrl = baseUrl;
    }

    public static getInstance(baseUrl?: string): FhirQueryBuilder {
        return new FhirQueryBuilder(baseUrl);
    }

    get resourceName(): string {
        return this._resourceName;
    }

    setResourceName(resourceName: string): FhirQueryBuilder {
        this._resourceName = resourceName;
        return this;
    }

    get query(): string {
        let q = `${this._baseUrl}`;
        switch (this._fhirOperation) {
            case TypeLevelInteractions.search:
                q = `${q}/${this._resourceName}?`;
                this.params.forEach(p => {
                    q = q + p;
                });
                break;
            case InstanceLevelInteractions.read:
                q = `${q}/${this._resourceName}/`;
                this.params.forEach(p => {
                    q = q + p;
                });
                break;
        }
        return q;
    }

    read(id: string): FhirQueryBuilder {
        this._fhirOperation = InstanceLevelInteractions.read;
        this._params.push(id);
        return this;
    }

    search(propName: string): LeftPart {
        this._fhirOperation = TypeLevelInteractions.search;
        const lp: LeftPart = new LeftPart(this);
        lp.left = propName;
        return lp;
    }

    get params(): any[] {
        return this._params;
    }

    and(): FhirQueryBuilder {
        this.params.push('&');
        return this;
    }

    append(val: string): FhirQueryBuilder {
        this.params.push(val);
        return this;
    }

    paramsAsObject() {
        const res = {};
        for (const se of this.params) {
            if (se === '&') continue;
            const paramParts: string[] = se.split('=');
            res[paramParts[0]] = paramParts[1];
        }
        return res;
    }

    paramsAsHttpParams(): HttpParams {
        let res = new HttpParams();
        for (const se of this.params) {
            if (se === '&') continue;
            const paramParts: string[] = se.split('=');
            res = res.append(paramParts[0], paramParts[1]);
        }
        return res;
    }
}

export class LeftPart {
    private readonly _qb: FhirQueryBuilder;
    left: string;

    constructor(qb: FhirQueryBuilder) {
        this._qb = qb;
    }

    eq(val: string): FhirQueryBuilder {
        this._qb.params.push(`${this.left}=${val}`);
        return this._qb;
    }

    contains(val: string) {
        this._qb.params.push(`${this.left}:contains=${val}`);
        return this._qb;
    }

    starts(val: string) {
        this._qb.params.push(`${this.left}=${val}`);
        return this._qb;
    }

    exact(val: string) {
        this._qb.params.push(`${this.left}:exact=${val}`);
        return this._qb;
    }
}
