import {BackboneElement} from './terminology/backboneelement';
import {Range} from './range';
import {Period} from './period';
import {CodeableConcept} from './terminology/codeableconcept';

export class Timing extends BackboneElement {
  constructor(source?: any) {
    super();
    if (source)
      for (const key of Object.keys(source)) {
        this[key] = source[key];
      }
  }
  event?: Date[];
  repeat?: { // Element
    boundsDuration?: Date;
    boundsRange?: Range;
    boundsPeriod?: Period;
    count?: number; // positiveInt Regex: +?[1-9][0-9]*
    countMax?: number;
    duration?: number; // decimal Regex: -?(0|[1-9][0-9]*)(\.[0-9]+)?([eE][+-]?[0-9]+)?
    durationMax?: number;
    durationUnit?: 's' | 'min' | 'h' | 'd' | 'wk' | 'mo' | 'a'; // unit of time (UCUM)
    frequency?: number; // positiveInt Regex: +?[1-9][0-9]*
    frequencyMax?: number;
    period?: number; // decimal Regex: -?(0|[1-9][0-9]*)(\.[0-9]+)?([eE][+-]?[0-9]+)?
    periodMax?: number;
    periodUnit?: 's' | 'min' | 'h' | 'd' | 'wk' | 'mo' | 'a'; // unit of time (UCUM)
    dayOfWeek?: string[];
    timeOfDay?: Date[]; // Regex: ([01][0-9]|2[0-3]):[0-5][0-9]:([0-5][0-9]|60)(\.[0-9]+)?
    when?: string[]; // Regex: [^\s]+(\s[^\s]+)*
    offset?: number; // unsignedInt Regex: [0]|([1-9][0-9]*)
  };
  code?: CodeableConcept;
}
