import {DomainResource} from '../domain-resource';
import {CodeableConcept, Identifier, Reference} from '..';
import {Annotation} from '../workflow/annotation';

export class List extends DomainResource {
    resourceType: string = 'List';
    identifier?: Identifier[];
    status: 'current' | 'retired' | 'entered-in-error';
    mode: 'working' | 'snapshot' | 'changes';
    title?: string;
    code?: CodeableConcept;
    subject?: Reference; // (Patient | Group | Device | Location)
    encounter?: Reference; // (Encounter)
    date?: Date;
    source?: Reference; // (Practitioner | PractitionerRole | Patient | Device)
    orderedBy?: CodeableConcept;
    note?: Annotation[];
    entry?: { // BackboneElement
        flag?: CodeableConcept;
        deleted?: boolean;
        date?: Date;
        item: Reference; // (Any)
    }[];
    emptyReason?: CodeableConcept;
}
