import {SearchModifier} from './search-modifier';

export class SearchItem {
    type: 'number' | 'date' | 'string' | 'token' | 'reference' | 'composite' | 'quantity' | 'uri' | 'special';
    key: string;
    modifier: SearchModifier;
    value: string;
    options: any;

    public static init(type, key, modifier, value): SearchItem {
        const item: SearchItem = new SearchItem();
        item.type = type;
        item.key = key;
        item.modifier = modifier;
        item.value = value;
        return item;
    }

    setType(value: 'number' | 'date' | 'string' | 'token' | 'reference' | 'composite' | 'quantity' | 'uri' | 'special'): SearchItem {
        this.type = value;
        return this;
    }

    setKey(value: string): SearchItem {
        this.key = value;
        return this;
    }

    setModifier(value: SearchModifier): SearchItem {
        this.modifier = value;
        return this;
    }

    setValue(value: string): SearchItem {
        this.value = value;
        return this;
    }

    setOptions(value: any): SearchItem {
        this.options = value;
        return this;
    }

    compileKey(): string {
        return this.modifier.compileKey(this);
    }

    compileValue(): string {
        return this.modifier.compileValue(this);
    }
}
