import {SearchItem} from './search-item';
import {SearchSetBuilder} from './search-set-builder';
import {HttpParams} from '@angular/common/http';

export class SearchSet {

    resourceName: string;
    searchItems: Array<SearchItem> = [];
    title: string;
    description: string;
    getpagesoffset: number;
    count: number;
    sort: string = '-_id';
    direction: 'asc' | 'desc';

    paramsAsObject() {
        let so = new HttpParams();
        for (const si of this.searchItems) {
            so = so.append(si.compileKey(), si.compileValue());
        }
        if (this.getpagesoffset) so.append('_getpagesoffset', String(this.getpagesoffset));
        if (this.count) so.append('_count', String(this.count));
        so.append('_sort', '-_id');
        return so;
    }

    build(): SearchSetBuilder {
        return new SearchSetBuilder(this);
    }
}
