import {Element} from '../element';

export class Coding extends Element {
    constructor(source?: any) {
        super();
        if (source)
            for (const key of Object.keys(source)) {
                this[key] = source[key];
            }
    }
    system?: string; // uri Regex: \S*
    version?: string; // Regex: [ \r\n\t\S]+
    code?: string; // Regex: [^\s]+(\s[^\s]+)*
    display?: string; // Regex: [ \r\n\t\S]+
    userSelected?: boolean; // Regex: true|false
}
