import {SearchSet} from './search-set';
import {SearchItem} from './search-item';

export class SearchSetBuilder {

    constructor(private ss: SearchSet) {
    }

    get(): SearchSet {
        return this.ss;
    }

    setResourceName(resource: string): SearchSetBuilder {
        this.ss.resourceName = resource;
        return this;
    }

    setTitle(value: string): SearchSetBuilder {
        this.ss.title = value;
        return this;
    }

    setDescription(value: string): SearchSetBuilder {
        this.ss.description = value;
        return this;
    }

    setGetpagesoffset(value: number): SearchSetBuilder {
        this.ss.getpagesoffset = value;
        return this;
    }

    setCount(value: number): SearchSetBuilder {
        this.ss.count = value;
        return this;
    }

    addSearchItem(): SearchItem {
        const si: SearchItem = new SearchItem();
        this.ss.searchItems.push(si);
        return si;
    }
}
