import {DomainResource} from '../domain-resource';
import {CodeableConcept, Identifier} from '../terminology';
import {Reference} from '../reference';
import {Money} from '../money';
import {Annotation} from '../workflow';

export class Invoice extends DomainResource {
    resourceType: string = 'Invoice';
    identifier?: Identifier[];
    status: 'draft' | 'issued' | 'balanced' | 'cancelled' | 'entered-in-error';
    cancelledReason?: string;
    type?: CodeableConcept;
    subject?: Reference;
    recipient?: Reference;
    date?: Date;
    participant?: { // BackboneElement
        role?: CodeableConcept;
        actor: Reference;
    }[];
    issuer?: Reference;
    account?: Reference;
    lineItem?: { // BackboneElement
        sequence?: number;
        chargeItemReference: Reference;
        chargeItemCodeableConcept: CodeableConcept;
        priceComponent?: { // BackboneElement
            type: 'base' | 'surcharge' | 'deduction' | 'discount' | 'tax' | 'informational';
            code?: CodeableConcept;
            factor?: number;
            amount?: Money;
        }[];
    }[];
    totalPriceComponent?: Reference[];
    totalNet?: Money;
    totalGross?: Money;
    paymentTerms?: string;
    note?: Annotation[];
}
