import {DomainResource} from '../domain-resource';
import {CodeableConcept, Identifier, SimpleQuantity} from '../terminology';
import {Reference} from '../reference';
import {Period} from '../period';
import {Money} from '../money';

export class Coverage extends DomainResource {
  identifier?: Identifier[];
  status: 'active' | 'cancelled' | 'draft' | 'entered-in-error';
  type?: CodeableConcept;
  policyHolder?: Reference; // (Patient, RelatedPerson, Organization)
  subscriber?: Reference; // (Patient, RelatedPerson)
  subscriberId?: string;
  beneficiary: Reference; // (Patient)
  dependent?: string;
  relationship?: CodeableConcept;
  period?: Period;
  payor: Reference[]; // (Organization | Patient | RelatedPerson)
  class?: {
    type: CodeableConcept;
    value: string;
    name?: string;
  }[];
  order?: string;
  network?: string;
  costToBeneficiary?: { // BackboneElement
    type?: CodeableConcept;
    valueQuantity: SimpleQuantity;
    valueMoney: Money;
    exception?: { // BackboneElement
      type: CodeableConcept;
      period?: Period;
    }[];
  }[];
  subrogation?: boolean;
  contract?: Reference[]; // (Contract)
}
