import {Element} from '../element';
export class Attachment extends Element {
  resourceType: string = 'Attachment';
  constructor(source?: any) {
    super();
    if (source)
      for (const key of Object.keys(source)) {
        this[key] = source[key];
      }
  }
  contentType?: string;
  language?: string;
  data?: string; // base64Binary Regex: (\s*([0-9a-zA-Z\+\=]){4}\s*)+
  url?: string; // url
  size?: number; // unsignedInt Regex: [0]|([1-9][0-9]*)
  hash?: string; // base64Binary Regex: (\s*([0-9a-zA-Z\+\=]){4}\s*)+
  title?: string;
  creation?: Date;

  // constructor(contentType, language, data, url, size, hash, title, creation) {
  //   super();
  //   this.contentType = contentType;
  //   this.language = language;
  //   this.data = data;
  //   this.url = url;
  //   this.size = size;
  //   this.hash = hash;
  //   this.title = title;
  //   this.creation = creation;
  // }
}
