import {DomainResource} from '../domain-resource';
import {Identifier} from './identifier';
import {ContactDetail} from './contactdetail';
import {UsageContext} from './usagecontext';
import {CodeableConcept} from './codeableconcept';
import {ConceptMapDependsOn} from './conceptmapdependson';

export class ConceptMap extends DomainResource {
    resourceType: string = 'ConceptMap';
    url?: string; // uri
    identifier?: Identifier;
    version?: string;
    name?: string;
    title?: string;
    status: 'draft' | 'active' | 'retired' | 'unknown';
    experimental?: boolean;
    date?: Date;
    publisher?: string;
    contact?: ContactDetail[];
    description?: string;
    useContext?: UsageContext[];
    jurisdiction?: CodeableConcept[];
    purpose?: string; // Regex: \s*(\S|\s)*
    copyright?: string; // Regex: \s*(\S|\s)*
    sourceUri?: string; // uri
    sourceCanonical?: string; // canonical(ValueSet)
    targetUri?: string; // uri
    targetCanonical?: string; // canonical(ValueSet)
    group?: {
        source?: string; // uri
        sourceVersion?: string;
        target?: string; // uri
        targetVersion?: string;
        element: {
            code?: string;
            display?: string;
            target?: {
                code?: string;
                display?: string;
                equivalence: string; // code
                comment?: string;
                dependsOn?: ConceptMapDependsOn[];
                product?: ConceptMapDependsOn[]; // see dependsOn
            }[];
            unmapped?: {
                mode: string; // code
                code?: string; // code
                display?: string;
                url?: string; // canonical(ConceptMap)
            }[]
        }[];
    }[];
}
