import {Inject, Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {FHIR_CONFIG, FhirConfig} from '../fhir-config';
import {FhirService} from './fhir-service';
import {Observable} from 'rxjs';
import {Encounter} from './model/management';

@Injectable({
    providedIn: 'root',
})
export class FhirAdvancedService extends FhirService {

    headers: any;
    fhircqrs: string;

    constructor(@Inject(FHIR_CONFIG) public config: FhirConfig, public httpClient: HttpClient) {
        super(config, httpClient);
        this.fhircqrs = config.fhircqrs;
    }

    public setAuthentication(headers: any) {
        this.headers = headers;
    }

    public searchInConcept(part: string, id: string, version?: string): Observable<ConceptDto[]> {
        return this.httpClient
            .get<ConceptDto[]>(`${this.fhircqrs}/CodeSystem/searchInConcept`,
                {headers: this.headers, params: {part: part, id: id, version: version}},
            );
    }

    public findActiveEncounterByPatient(patientId: string): Observable<Encounter[]> {
        return this.httpClient
            .get<Encounter[]>(`${this.fhircqrs}/Encounter/findActiveEncounterByPatient`,
                {headers: this.headers, params: {patientId: patientId}},
            );
    }

}

export class ConceptDto {
    system: string;
    code: string;
    display: string;
    definition: string;
}
