import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'fhirTelecomPhoneExtract'
})
export class FhirTelecomPhoneExtract implements PipeTransform {

  transform(value: any, ...args: any[]): string {
    let phone: string = '';
    if (value && value.length > 0) {
      for (let contactPoint of value) {
        if (contactPoint.system === 'phone') phone = phone.concat(contactPoint.value, ', ');
      }
    }
    return phone.trim().slice(0,phone.length-2);
  }

}
