import {DomainResource} from '../domain-resource';
import {Address, CodeableConcept, ContactPoint, HumanName, Identifier, Reference} from '..';

export class Organization extends DomainResource {
  resourceType: string = 'Organization';
  identifier?: Identifier[];
  active?: boolean;
  type?: CodeableConcept[];
  name?: string;
  alias?: string[];
  telecom?: ContactPoint[];
  address?: Address[];
  partOf?: Reference; // (Organization)
  contact?: { // BackboneElement
    purpose?: CodeableConcept;
    name?: HumanName;
    telecom?: ContactPoint[];
    address?: Address;
  }[];
  endpoint?: Reference; // (Endpoint)
}
