import { Quantity } from '../terminology/quantity';

export class Duration extends Quantity {
    constructor(source?: any) {
        super();
        if (source)
            for (const key of Object.keys(source)) {
                this[key] = source[key];
            }
    }
    value?: number; // decimal 	Regex: -?(0|[1-9][0-9]*)(\.[0-9]+)?([eE][+-]?[0-9]+)?
    comparator?: '<' | '<=' | '>=' | '>';
    unit?: string;
    system?: string; // uri
    code?: string; // Regex: [^\s]+(\s[^\s]+)*
}
