import {DomainResource} from '../domain-resource';
import {Address, CodeableConcept, Coding, ContactPoint, Identifier, Reference} from '..';

export class Location extends DomainResource {
  resourceType: string = 'Location';
  identifier?: Identifier[];
  status?: 'active' | 'suspended' | 'inactive';
  operationalStatus?: Coding;
  name?: string;
  alias?: string[];
  description?: string;
  mode?: 'instance' | 'kind';
  type?: CodeableConcept[];
  telecom?: ContactPoint;
  address?: Address;
  physicalType?: CodeableConcept;
  position?: {
    longitude: number; // decimal	Regex: -?(0|[1-9][0-9]*)(\.[0-9]+)?([eE][+-]?[0-9]+)?
    latitude: number;
    altitude?: number;
  }[];
  managingOrganization?: Reference; // (Organization)
  partOf?: Reference; // (Location)
  hoursOfOperation?: {
    daysOfWeek?: string[];
    allDay?: boolean;
    openingTime?: Date; // Regex: ([01][0-9]|2[0-3]):[0-5][0-9]:([0-5][0-9]|60)(\.[0-9]+)?
    closingTime?: Date;
  }[];
  availabilityExceptions?: string;
  endpoint?: Reference; // (Endpoint)
}
