import {DomainResource} from '../domain-resource';
import {Identifier} from '../terminology/identifier';
import {CodeableConcept} from '../terminology/codeableconcept';
import {Reference} from '../reference';
import {Quantity} from '../terminology/quantity';
import {Range} from '../range';
import {Period} from '../period';

export class Group extends DomainResource {
    resourceType: string = 'Group';
    identifier?: Identifier[];
    active?: boolean;
    type: 'person' | 'animal' | 'practitioner' | 'device' | 'medication' | 'substance';
    actual: boolean;
    code?: CodeableConcept;
    name?: string;
    quantity?: number; // unsignedInt  	Regex: [0]|([1-9][0-9]*)
    managingEntity?: Reference; // (Organization | RelatedPerson | Practitioner | PractitionerRole)
    characteristic?: { // BackboneElement
        code: CodeableConcept;
        valueCodeableConcept?: CodeableConcept;
        valueBoolean?: boolean;
        valueQuantity?: Quantity;
        valueRange?: Range;
        valueReference?: Reference;
        exclude: boolean;
        period?: Period;
    }[];
    member?: { // BackboneElement
        entity: Reference; // (Patient | Practitioner | PractitionerRole | Device | Medication | Substance | Group)
        period?: Period;
        inactive?: boolean;
    }[];
}
