import {Inject, Injectable} from '@angular/core';
import {Observable, of} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {FhirQueryBuilder} from './query-builder/fhir-query-builder';
import {SearchSet} from './search-set/search-set';
import {BundleEntry, CapabilityStatement, OperationOutcome} from './model';
import {FHIR_CONFIG, FhirConfig} from '../fhir-config';
import {tap} from 'rxjs/operators';
import {Bundle} from './model';
import {merge} from 'lodash';

@Injectable({
    providedIn: 'root',
})
export class FhirService {
    public headers;
    private conformance: CapabilityStatement;

    constructor(@Inject(FHIR_CONFIG) public config: FhirConfig, public httpClient: HttpClient) {
    }

    set base(base: string) {
        this.config.base = base;
    }

    getConformanceStatement(headers?): Observable<CapabilityStatement> {
        const mergedHeaders = Object.assign(this.headers, headers);
        if (this.conformance) return of(this.conformance);
        return this.httpClient.get<CapabilityStatement>(`${this.config.base}/metadata`, {headers: mergedHeaders}).pipe(
            tap(result => this.conformance = result),
        );
    }

    public read<T>(resourceType: string, id: string, headers?): Observable<T> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.get<T>(`${this.config.base}/${resourceType}/${id}`, {headers: mergedHeaders});
    }

    public vread<T>(resourceType: string, id: string, v: number, headers?): Observable<T> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.get<T>(`${this.config.base}/${resourceType}/${id}/_history/${v}`, {headers: mergedHeaders});
    }

    public update<T>(resource: any, headers?): Observable<T> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.put<T>(`${this.config.base}/${resource.resourceType}/${resource.id}`, resource, {headers: mergedHeaders});
    }

    public save<T>(resource: any, headers?): Observable<T> {
        const mergedHeaders = Object.assign(this.headers, headers);
        const resourceType = resource.resourceType;
        if (resource.hasOwnProperty('id')) {
            return this.update(resource, headers);
        } else {
            return this.create(resource, headers);
        }
    }

    public patch<T>(resource: any, body: any, headers?): Observable<T> {
        const headersToAttach = {};
        merge(headersToAttach, this.headers);
        headersToAttach['Content-Type'] = 'application/json-patch+json';
        return this.httpClient.patch<T>(`${this.config.base}/${resource.resourceType}/${resource.id}`, body, {headers: headersToAttach});
    }

    public delete<T>(resourceType: string, id: string, headers?): Observable<OperationOutcome> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.delete<OperationOutcome>(`${this.config.base}/${resourceType}/${id}`, {headers: mergedHeaders});
    }

    public create<T>(resource: any, headers?): Observable<T> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.post<T>(`${this.config.base}/${resource.resourceType}`, resource, {headers: mergedHeaders});
    }

    // ToDo:
    public searchByQuery(qb: FhirQueryBuilder, headers?): Observable<Bundle> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.get<Bundle>(`${this.config.base}/${qb.resourceName}`, {
            params: qb.paramsAsHttpParams(),
            headers: mergedHeaders
        });
    }

    public searchBySet<T>(ss: SearchSet, headers?): Observable<Bundle> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.get<Bundle>(`${this.config.base}/${ss.resourceName}?_sort=-_id`, {
            params: ss.paramsAsObject(),
            headers: mergedHeaders,
        });
    }

    public searchByUrl<T>(url: string, headers?): Observable<T> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.get<T>(url, {headers: mergedHeaders});
    }

    public search_all() {

    }

    public capabilities() {

    }

    public transaction(bundle: Bundle, headers?): Observable<any> {
        const mergedHeaders = Object.assign(this.headers, headers);
        return this.httpClient.post(this.config.base, bundle,
            {
                headers: mergedHeaders
            });
    }

    public history() {

    }

    public history_type() {

    }

    public history_all() {

    }

    public executeQuery<T>(qb: FhirQueryBuilder): Observable<T> {
        return this.httpClient.get<T>(qb.query, {headers: this.headers});
    }

    public createTransactionBundle(source: any, keys: string[]): Bundle {
        const bundle: Bundle = new Bundle();
        bundle.type = 'transaction';
        bundle.resourceType = 'Bundle';
        bundle.entry = [];
        keys.filter(key => source.hasOwnProperty(key))
            .map(key => {
                const v = source[key];
                if (this.isIterable(v)) {
                    for (const e of v) {
                        bundle.entry.push(this.createBundleEntry(e));
                    }
                } else {
                    bundle.entry.push(this.createBundleEntry(v));
                }
            });
        return bundle;
    }

    private isIterable(value) {
        return value !== null && Symbol.iterator in Object(value);
    }

    private createBundleEntry(entry): BundleEntry {
        return {
            fullUrl: `urn:uuid:${entry.uuid}`,
            resource: entry,
            request: {
                url: !entry.id ? entry.resourceType : `${entry.resourceType}/${entry.id}`,
                method: entry.id ? 'PUT' : 'POST',
            },
        };
    }

    /*generateCodeableConcept(code: string, system?: string, display?: string): CodeableConcept {
        const codeableConcept: CodeableConcept = new CodeableConcept();
        codeableConcept.coding = [{code: code}];
        if (system) codeableConcept.coding[0].system = system;
        if (display) codeableConcept.coding[0].display = display;
        return codeableConcept;
    }

    generateIdentifier(): Identifier {
        const identifier: Identifier = new Identifier();
        identifier.system = FhirService.IdentifierSystem;
        identifier.value = UUID.UUID();
        return identifier;
    }*/
}

