import { DomainResource } from '../domain-resource';
import { Identifier } from '../terminology/identifier';
import { Reference } from '../reference';
import { CodeableConcept } from '../terminology/codeableconcept';
import { Attachment } from '../individuals/attachment';
import { ContactPoint } from '../terminology/contactpoint';
import { Period } from '../period';

export class HealthcareService extends DomainResource {
    constructor(source?: any) {
        super();
        if (source)
            for (const key of Object.keys(source)) {
                this[key] = source[key];
            }
    }
    resourceType: string = 'HealthcareService';
    identifier?: Identifier[];
    active?: boolean;
    providedBy?: Reference; // (Organization)
    category?: CodeableConcept[];
    type?: CodeableConcept[];
    specialty?: CodeableConcept[];
    location?: Reference[]; // (Location)
    name?: string;
    comment?: string;
    extraDetails?: string; // Regex: \s*(\S|\s)*
    photo?: Attachment;
    telecom?: ContactPoint[];
    coverageArea?: Reference; // (Location)
    serviceProvisionCode?: CodeableConcept[];
    eligibility?: { // BackboneElement
        code?: CodeableConcept;
        comment?: string;
    }[];
    program?: CodeableConcept[];
    characteristic?: CodeableConcept[];
    communication?: CodeableConcept[];
    referralMethod?: CodeableConcept[];
    appointmentRequired?: boolean;
    availableTime?: {
        daysOfWeek?: string[];
        allDay?: boolean;
        availableStartTime?: Date; // Regex: ([01][0-9]|2[0-3]):[0-5][0-9]:([0-5][0-9]|60)(\.[0-9]+)?
        availableEndTime?: Date;
    }[];
    notAvailable?: { // BackboneElement
        description: string;
        during?: Period;
    }[];
    availabilityExceptions?: string;
    endpoint?: Reference[]; // (Endpoint)

    public static getNew() {
        return new HealthcareService({name: [HealthcareService.getNew()]});
    }
}
